import test from "ava"
import http from "ava-http"





test("/GET incidents", async t => {

    const resp = await http.get("http://localhost:8000/incidents");
    
    t.true(typeof resp === typeof [], "The response is not an object");
});


test("/GET localities", async t => {

    const resp = await http.get("http://localhost:8000/localities");
    console.log(resp)
    t.true(typeof resp === typeof [], "The response is not an object");
});


